export function giveMeSomeMoves(count) {
    const moves = [];
    while(true) {
        var newMoves = Math.random().toString().slice(2).split('').map(x => (x % 4) + 1);
        for(const m of newMoves) {
            moves.push(m);
            if(moves.length === count) return moves;
        }
    }
     
}

export const audio1 = new Audio('https://danyo1399.github.io/simon-says/assets/simonSound1.mp3')
audio1.loop = false
export const audio2 = new Audio('https://danyo1399.github.io/simon-says/assets/simonSound2.mp3')
audio2.loop = false
export const audio3 = new Audio('https://danyo1399.github.io/simon-says/assets/simonSound3.mp3')
audio3.loop = false
export const audio4 = new Audio('https://danyo1399.github.io/simon-says/assets/simonSound4.mp3')
audio4.loop = false


function play(audio) {
    const p =  new Promise((resolve, reject) => {
        function ended() {
            audio.onended = null;
            resolve()
        }
        audio.onended = ended;
        audio.play();
    })

    return p;
}
export function playAudio(position) {
    switch(position) {
        case 1: {
            return play(audio1);
        }
        case 2: {
            return play(audio2);
        }
        case 3: {
            return play(audio3);
        }
        case 4: {
            return play(audio4);
        }
    }
}

export function stopAudio(position) {
    switch(position) {
        case 1: {
            return audio1.pause();
        }
        case 2: {
            return audio2.pause();
        }
        case 3: {
            return audio3.pause();
        }
        case 4: {
            return audio4.pause();
        }
    }
}